import * as React from 'react';
import { Card, CardBody, CardText, CardTitle, Col, Container, Row } from 'reactstrap';

import './Works.css';

class Works extends React.Component {
    constructor(props: {}) {
        super(props);
    }

    public render() {
        return (
            <main className="main">
                <h1 className="works-page__title">Works</h1>
                <Container fluid={true} className="works-page__container">
                    <Row>
                        <Col xs="12" md="4">
                            <Card className="works-page__card">
                                <CardBody>
                                    <CardTitle>CookieSound</CardTitle>
                                    <CardText>
                                        C# + Firebase<br />
                                        FirebaseGUI
                                        IRC
                                    </CardText>
                                    <div className="works-page__source-link">
                                        <a href="https://github.com/roottool/Cookiesound-kari-"></a>
                                    </div>
                                </CardBody>
                            </Card>
                        </Col>
                        <Col xs="12" md="4">
                            <Card className="works-page__card">
                                <CardBody>
                                    <CardTitle>CookieSound</CardTitle>
                                    <CardText>
                                        Python<br />
                                        CUI
                                        IRC
                                    </CardText>
                                    <div className="works-page__source-link">
                                        <a href="https://github.com/roottool/CookieSound2"></a>
                                    </div>
                                </CardBody>
                            </Card>
                        </Col>
                        <Col xs="12" md="4">
                            <Card className="works-page__card">
                                <CardBody>
                                    <CardTitle>OrgaSound</CardTitle>
                                    <CardText>
                                        Angular 6 + Firebase<br />
                                        FirebaseWeb
                                        Firebase
                                    </CardText>
                                    <div className="works-page__source-link">
                                        <a href="https://github.com/roottool/OrgaSound"></a>
                                    </div>
                                </CardBody>
                            </Card>
                        </Col>
                    </Row>
                    <Row>
                        <Col xs="12" md="4">
                            <Card className="works-page__card">
                                <CardBody>
                                    <CardTitle>Portfolio site</CardTitle>
                                    <CardText>
                                        React<br />
                                        
                                        React
                                        
                                    </CardText>
                                    <div className="works-page__source-link">
                                        <a href="https://github.com/roottool/MyPortfolio"></a>
                                    </div>
                                </CardBody>
                            </Card>
                        </Col>
                    </Row>
                </Container>
            </main>
        );
    }
}

export default Works;


